import React from 'react'
import PropTypes from 'prop-types'
// import { Link } from 'react-router-dom'

export default function NavBar(props) {
  return (
    
    <div>
         <nav className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode}`}>
  <div className="container-fluid">
    <a className="navbar-brand" href="#" >{props.title}</a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <a className="nav-link active bold" aria-current="page" href="#">Home</a>
        </li>
        {/* <li className="nav-item">
          <a className="nav-link" href="/">About</a>
        </li> */}
        
        
      </ul>
      <div className={`form-check form-switch text-${props.mode==='light'?'dark':'white'}`}>
  <input className="form-check-input" type="radio" name="flexRadioDefault" onClick={props.toggleModegreen} id="flexRadioDefault1"/>
  <label className="form-check-label" htmlFor="flexRadioDefault1">
    Green
  </label>
</div>
<div className={`form-check form-switch text-${props.mode==='light'?'dark':'white'}`}>
  <input className="form-check-input" type="radio" name="flexRadioDefault" onClick={props.toggleModegrey} id="flexRadioDefault2" />
  <label className="form-check-label" htmlFor="flexRadioDefault2">
    Grey
  </label>
</div>
      <div className={`form-check form-switch text-${props.mode==='light'?'dark':'light'}`}>
  <input className="form-check-input" type="checkbox" onClick={props.toggleMode} role="switch" id="flexSwitchCheckDefault" aria-checked/>
  <label className="form-check-label" htmlFor="flexSwitchCheckDefault">Dark Mode</label>
</div>
      <form className="d-flex" role="search">
        <input className="form-control me-2 style={{fontWeight: 'bold'}}" type="search" placeholder="Search" aria-label="Search"/>
        <button className="btn btn-danger style={{fontWeight: 'bold'}}" type="submit">Search</button>
      </form>
    </div>
    
   

  </div>
 
</nav>
</div>


    
  )
}

NavBar.propTypes={title: PropTypes.string}

NavBar.defaultProps={title:"set title"}
